@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra {
    background-color: v.$azul;
    position: sticky;
    top: -.1rem;
    width: 100%;
    z-index: 10;

    &__contenido {
        @include m.contenedor;
        display: flex;
        flex-direction: column;

        @include m.desktop{
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
        }
    }

    &__logo {
        @include m.logo;
        @include m.texto-fluido(2rem, 3rem);
    }
}